import React, { useState, useEffect, Suspense } from "react";
// 1. Import BrowserRouter. Remove Redirect.
import { BrowserRouter } from "react-router-dom";
import "./App.css";
import Main from "./containers/Main";
import { ThemeProvider } from "styled-components";
import { themes } from "./theme";
import { GlobalStyles } from "./global";
import { CursorProvider } from "react-cursor-custom";
import { settings } from "./portfolio";
import Splash from "./pages/splash/Splash";

const Background3D = React.lazy(() =>
  import("./components/Background3D/Background3D")
);

function App() {
  // 2. This ensures Splash runs on EVERY reload/visit
  const [isSplashVisible, setIsSplashVisible] = useState(settings.isSplash);

  const [windowSize, setWindowSize] = useState({
    width: window.innerWidth,
    height: window.innerHeight,
  });

  const [showParticles, setShowParticles] = useState(() => {
    const savedParticles = localStorage.getItem("useParticles");
    if (savedParticles === null) {
      localStorage.setItem(
        "useParticles",
        JSON.stringify(settings.useParticles)
      );
      return settings.useParticles;
    }
    return JSON.parse(savedParticles);
  });

  useEffect(() => {
    const handleResize = () =>
      setWindowSize({ width: window.innerWidth, height: window.innerHeight });
    window.addEventListener("resize", handleResize);
    return () => window.removeEventListener("resize", handleResize);
  }, []);

  const [theme, setTheme] = useState(() => {
    const savedTheme = localStorage.getItem("theme");
    return savedTheme ? savedTheme : "dark";
  });

  const useCursor = settings.useCustomCursor;

  const toggleParticles = () => {
    const newValue = !showParticles;
    setShowParticles(newValue);
    localStorage.setItem("useParticles", JSON.stringify(newValue));
  };

  // 3. When splash ends, we just hide the overlay.
  // We do NOT redirect. The URL stays whatever the user typed.
  const handleSplashComplete = () => {
    setIsSplashVisible(false);
  };

  return (
    <BrowserRouter basename="/">
      <ThemeProvider theme={themes[theme]}>
        <>
          <GlobalStyles />

          {/* 4. SPLASH SCREEN */}
          {/* It sits on top of everything because of z-index in CSS */}
          {isSplashVisible && (
            <Splash theme={themes[theme]} onComplete={handleSplashComplete} />
          )}

          {/* 5. MAIN CONTENT */}
          {/* This renders the correct page (Projects, Home, etc.) immediately behind the splash */}
          <div
            className="main-content-layer"
            style={{
              pointerEvents: isSplashVisible ? "none" : "auto",
            }}
          >
            <Suspense fallback={<></>}>
              {showParticles && <Background3D />}
            </Suspense>

            <div className="app-container">
              <div className="particles-toggle-wrapper">
                <button
                  className="particles-toggle-button"
                  onClick={toggleParticles}
                  style={{
                    backgroundColor: themes[theme].accentColor,
                    color: "#fff",
                    boxShadow: `0 2px 10px ${themes[theme].accentColor}40`,
                  }}
                >
                  <span className="particles-label-hover">
                    {showParticles ? "Disable Particles" : "Enable Particles"}
                  </span>
                  <span
                    className={`toggle-icon ${
                      showParticles ? "enabled" : "disabled"
                    }`}
                  >
                    {showParticles ? "" : ""}
                  </span>
                </button>
              </div>

              {useCursor ? (
                <CursorProvider
                  color={themes[theme].secondaryText}
                  ringSize={25}
                  transitionTime={75}
                >
                  <Main theme={themes[theme]} setTheme={setTheme} />
                </CursorProvider>
              ) : (
                <Main theme={themes[theme]} setTheme={setTheme} />
              )}
            </div>
          </div>
        </>
      </ThemeProvider>
    </BrowserRouter>
  );
}

export default App;
