import React, { lazy, Suspense, useState } from "react";
import { Route, Switch } from "react-router-dom"; // Removed BrowserRouter
// Removed Splash import (It's in App.js now)

// Lazy imports
const Home = lazy(() => import("../pages/home/HomeComponent"));
const Education = lazy(() => import("../pages/education/EducationComponent"));
const Experience = lazy(() => import("../pages/experience/Experience"));
const Contact = lazy(() => import("../pages/contact/ContactComponent"));
const Projects = lazy(() => import("../pages/projects/Projects"));
const GithubPage = lazy(() => import("../pages/github/GithubPage"));
const PortfolioChatBot = lazy(() => import("../components/chatbot/ChatBot"));

// Admin & User Pages
const AdminDashboard = lazy(() => import("../pages/admin/AdminDashboard"));
const UserSpace = lazy(() => import("../pages/userspace/UserSpace"));

export default function Main(propss) {
  const [isChatOpen, setIsChatOpen] = useState(false);

  // Loading fallback
  const loadingFallback = (
    <div
      style={{
        height: "100vh",
        width: "100vw",
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        background: propss.theme?.body || "#0a0a0a",
        color: propss.theme?.text || "#ffffff",
        fontSize: "18px",
      }}
    ></div>
  );

  return (
    <div className="main-content-container">
      {/* 
         NOTE: BrowserRouter is removed from here. 
         It is now in App.js wrapping everything.
      */}
      <Suspense fallback={loadingFallback}>
        <Switch>
          {/* User Space */}
          <Route
            path="/myspace"
            render={(props) => (
              <UserSpace
                {...props}
                theme={propss.theme}
                setTheme={propss.setTheme}
              />
            )}
          />

          {/* Admin */}
          <Route
            path="/admin"
            render={(props) => (
              <AdminDashboard
                {...props}
                theme={propss.theme}
                setTheme={propss.setTheme}
              />
            )}
          />

          {/* 
             HOME ROUTES:
             Crucial: We render Home on both "/" and "/home".
             This ensures content is visible behind the splash screen.
          */}
          <Route
            path="/"
            exact
            render={(props) => (
              <Home
                {...props}
                theme={propss.theme}
                setTheme={propss.setTheme}
              />
            )}
          />
          <Route
            path="/home"
            render={(props) => (
              <Home
                {...props}
                theme={propss.theme}
                setTheme={propss.setTheme}
              />
            )}
          />

          {/* Other Pages */}
          <Route
            path="/experience"
            exact
            render={(props) => (
              <Experience
                {...props}
                theme={propss.theme}
                setTheme={propss.setTheme}
              />
            )}
          />
          <Route
            path="/education"
            render={(props) => (
              <Education
                {...props}
                theme={propss.theme}
                setTheme={propss.setTheme}
              />
            )}
          />
          <Route
            path="/contact"
            render={(props) => (
              <Contact
                {...props}
                theme={propss.theme}
                setTheme={propss.setTheme}
              />
            )}
          />
          <Route
            path="/projects"
            render={(props) => (
              <Projects
                {...props}
                theme={propss.theme}
                setTheme={propss.setTheme}
              />
            )}
          />
          <Route
            path="/github"
            render={(props) => (
              <GithubPage
                {...props}
                theme={propss.theme}
                setTheme={propss.setTheme}
              />
            )}
          />
        </Switch>

        <PortfolioChatBot theme={propss.theme} />
      </Suspense>
    </div>
  );
}
