import React, { useState, useEffect, useCallback, useRef } from "react";
import "./Splash.css";

const greetings = [
  "Hello",
  "",
  "Bonjour",
  "Hola",
  "Ciao",
  "",
  "Ol",
  "",
  "Hallo",
];

const FINAL_WORD = "Welcome";
const FIRST_DELAY = 700;
const WORD_DELAY = 130;
const FINAL_STAGGER = 600;
const FINAL_HOLD = 500;
const EXIT_DURATION = 900;
const TOTAL_DURATION =
  FIRST_DELAY +
  (greetings.length - 1) * WORD_DELAY +
  FINAL_STAGGER +
  FINAL_HOLD;

function Splash({ onComplete }) {
  const [index, setIndex] = useState(0);
  const [phase, setPhase] = useState("words"); // words  final  exiting  done
  const [counter, setCounter] = useState(0);
  const timerRef = useRef(null);
  const rafRef = useRef(null);
  const hasCompleted = useRef(false);
  const mountTime = useRef(Date.now());

  const finish = useCallback(() => {
    if (hasCompleted.current) return;
    hasCompleted.current = true;
    onComplete?.();
  }, [onComplete]);

  //  Skip for returning visitors 
  useEffect(() => {
    if (sessionStorage.getItem("splashSeen")) {
      finish();
      return;
    }
    sessionStorage.setItem("splashSeen", "true");
  }, [finish]);

  //  Animated counter: 0  100 (eased) 
  useEffect(() => {
    const animate = () => {
      const elapsed = Date.now() - mountTime.current;
      const progress = Math.min(elapsed / TOTAL_DURATION, 1);
      const eased = 1 - Math.pow(1 - progress, 3); // ease-out cubic
      setCounter(Math.floor(eased * 100));

      if (progress < 1 && !hasCompleted.current) {
        rafRef.current = requestAnimationFrame(animate);
      }
    };
    rafRef.current = requestAnimationFrame(animate);
    return () => cancelAnimationFrame(rafRef.current);
  }, []);

  //  State machine: word cycling 
  useEffect(() => {
    if (phase === "done" || hasCompleted.current) return;

    if (phase === "words") {
      const delay = index === 0 ? FIRST_DELAY : WORD_DELAY;
      timerRef.current = setTimeout(() => {
        if (index < greetings.length - 1) {
          setIndex((i) => i + 1);
        } else {
          setPhase("final");
        }
      }, delay);
    }

    if (phase === "final") {
      timerRef.current = setTimeout(() => {
        setPhase("exiting");
      }, FINAL_STAGGER + FINAL_HOLD);
    }

    if (phase === "exiting") {
      timerRef.current = setTimeout(() => {
        setPhase("done");
        finish();
      }, EXIT_DURATION);
    }

    return () => clearTimeout(timerRef.current);
  }, [index, phase, finish]);

  //  Click to skip 
  const handleSkip = useCallback(() => {
    clearTimeout(timerRef.current);
    cancelAnimationFrame(rafRef.current);
    setCounter(100);
    setPhase("exiting");
    setTimeout(() => finish(), EXIT_DURATION);
  }, [finish]);

  if (phase === "done") return null;

  const isExiting = phase === "exiting";

  return (
    <div
      className={`splash ${isExiting ? "splash--exit" : ""}`}
      onClick={handleSkip}
    >
      {/* Film grain overlay */}
      <div className="splash__grain" aria-hidden="true" />

      {/* Ambient center glow */}
      <div className="splash__ambient" aria-hidden="true" />

      {/*  Center: Word Display  */}
      <div className="splash__center">
        {phase === "words" && (
          <div className="word-mask" key={index}>
            <h1
              className={`word ${index === 0 ? "word--first" : "word--rapid"}`}
            >
              <span className="word__text">{greetings[index]}</span>
              <span className="word__dot" aria-hidden="true" />
            </h1>
          </div>
        )}

        {(phase === "final" || isExiting) && (
          <div className="word-mask">
            <h1 className="word word--final">
              {FINAL_WORD.split("").map((char, i) => (
                <span
                  key={i}
                  className="word__letter"
                  style={{ animationDelay: `${i * 0.06}s` }}
                >
                  {char}
                </span>
              ))}
              <span className="word__dot word__dot--final" aria-hidden="true" />
            </h1>
          </div>
        )}
      </div>

      {/*  Counter: bottom-left  */}
      <div className="splash__counter" aria-hidden="true">
        {counter}
        <span className="splash__percent">%</span>
      </div>

      {/*  Progress bar: bottom edge  */}
      <div className="splash__progress" aria-hidden="true">
        <div
          className="splash__bar"
          style={{ transform: `scaleX(${counter / 100})` }}
        />
      </div>
    </div>
  );
}

export default React.memo(Splash);
